<template>
    <div class='privateLimit'>
        <div class='top'>
          <span class='fundInfo' @click='goback()'>私教种类</span>
          <img src='../../assets/images/icon_nav_arrow.png'/>
          <span class='branch'>私教课预约限制</span>
        </div>
        <div class='limitInfo'>
            <div class="operation">
              <div class="edit" v-if="flag" @click="edit()">
                  <i class="iconfont icon-btn_edit"></i>
                  <span>编辑</span>
              </div>
              <div class="cancle" v-if="!flag" @click="cancle()">
                取消
              </div> 
              <div class="sub" v-if="!flag" @click="submit()">
                提交
              </div>
            </div>
            <div class="from">
                <p class="formNew">
                  <span>限制可提前预约私教课程数：</span>
                  <span class="right">
                    <input type="text" v-model="privateNum" placeholder="" class="textInp" maxlength="3" @blur="blur(privateNum,1)" v-show="!flag"/>
                    <div class="textInp" v-show="flag">{{privateNum}}</div>
                    <span>节</span>
                  </span>
                </p>
                <p class="formNew">
                  <span>限制可提前预约特色课程数：</span>
                  <span class="right">
                    <input type="text" v-model="specialNum" placeholder="" class="textInp"  maxlength="3" @blur="blur(specialNum,2)" v-show="!flag"/>
                    <div class="textInp" v-show="flag">{{specialNum}}</div>
                    <span>节</span>
                  </span>
                </p>
                <p class="formNew">
                  <span>限制会员在私教/特色课开始前多少分钟不能预约：</span>
                  <span class="right">
                    <input type="text" v-model="privateMinutes" placeholder="" class="textInp" maxlength="3" @blur="blur(privateMinutes,3)" v-show="!flag"/>
                    <div class="textInp" v-show="flag">{{privateMinutes}}</div>
                    <span>分钟</span>
                  </span>
                </p>
                <p class="formNew">
                  <span>限制会员在私教/特色课开始前多少分钟不能取消预约：</span>
                  <span class="right">
                    <input type="text" v-model="privateCancleMinutes" placeholder="" class="textInp" maxlength="3" @blur="blur(privateCancleMinutes,4)" v-show="!flag"/>
                    <div class="textInp" v-show="flag">{{privateCancleMinutes}}</div>
                    <span>分钟</span>
                  </span>
                </p>
            </div>
        </div>
    </div>
</template>
<script>
import {updateConfig, getConfig} from '../../services/service.js'
import {isApiResponseOk, setSessionStorage} from '../../utils/com.js'
export default {
  data () {
    return {
      flag: true,
      privateNum: null,
      specialNum: null,
      privateMinutes: null,  // 私教课特色课时间
      privateCancleMinutes: null, // 取消私教特色时间
      courseType: null,
      obj: {}
    }
  },
  created () {
    let that = this
    getConfig().then(function (res){
      that.obj = res.data
      that.privateNum = res.data.reservationPrivationNum
      that.specialNum = res.data.reservationSpecialNum
      that.privateMinutes = res.data.reservationPrivationEndTime
      that.privateCancleMinutes = res.data.reservationCancelPrivationEndTime
    })
  },
  methods: {
    edit () {
      this.flag = false
    },
    cancle () {
      this.flag = true
      this.privateNum = this.obj.reservationPrivationNum
      this.specialNum = this.obj.reservationSpecialNum
      this.privateMinutes = this.obj.reservationPrivationEndTime
      this.privateCancleMinutes = this.obj.reservationCancelPrivationEndTime
    },
    // 返回到私教课程页面
    goback () {
      this.$router.push({name: 'privatekind'})
    },
    blur (num,num2) {
      // console.log(parseInt(num).toString())
      // if (parseInt(num).toString() == 'NaN' || parseInt(num) < 0) {
      //   this.$message({
      //     showClose: true,
      //     message: '只能输入正整数',
      //     type: 'warning'
      //   })
      //   switch (num2) {
      //     case 1:
      //       this.privateNum = null
      //       break;
      //     case 2:
      //       this.specialNum = null
      //       break;
      //     case 3:
      //       this.privateMinutes = null
      //       break;
      //     case 4:
      //       this.privateCancleMinutes = null
      //       break;
      //     default: 
      //       this.privateNum = null
      //   }
      // }
    },
    submit () {
      let self = this
      this.checkNum()
      if (this.privateNum || this.specialNum || this.privateMinutes || this.privateCancleMinutes) {
        this.courseType = 'PRIVATE'
      }
      let obj = {
        courseType: this.courseType,
        reservationPrivationNum: this.privateNum,
        reservationSpecialNum: this.specialNum,
        reservationPrivationEndTime: this.privateMinutes,
        reservationSpecialEndTime: this.privateMinutes,
        reservationCancelPrivationEndTime: this.privateCancleMinutes,
        reservationCancelSpecialEndTime: this.privateCancleMinutes
      }
      updateConfig(obj).then(function (res) {
        // console.log('res',res)
        if (isApiResponseOk(res)) {
          self.$message({
            showClose: true,
            message: '更新成功',
            type: 'success'
          })
          self.flag = true
          // self.$router.push({name: 'privateLimit'})
        } else {
          self.$message({
            showClose: true,
            message: res.errMsg,
            type: 'warning'
          })
        }  
      })
    },
    checkNum () {
      if (this.privateNum) {
        this.privateNum = parseInt(this.privateNum)
      }
      if (this.specialNum) {
        this.specialNum = parseInt(this.specialNum)
      }
      if (this.privateMinutes) {
        this.privateMinutes = parseInt(this.privateMinutes)
      }
      if (this.privateCancleMinutes) {
        this.privateCancleMinutes = parseInt(this.privateCancleMinutes)
      }
    }
  }
}
</script>
<style lang="sass" scoped>
.privateLimit
  .top
    margin-bottom: .2rem;
    display: flex;
    align-items: center;
    .fundInfo
        font-size: .17rem;
        color: #8597A1;
        cursor: pointer;
    .branch
        font-size: .24rem;
        font-family: PingFangSC-Semibold;
        color: #263238;
    img
        width: .16rem;
        margin-left: .12rem;
        margin-right: .2rem;
  .limitInfo
    background: #fff;
    .operation
      width: 100%;
      height:  0.6rem;
      display: flex;
      justify-content: flex-end;
      align-items: center;
      border-bottom: 1px solid rgba(216,216,216,0.3);
      .cancle,.sub
        width: 1rem;
        height: 0.36rem;
        line-height: 0.36rem;
        text-align: center;
        border-radius: 0.04rem;
        cursor: pointer;
        font-size: .16rem;
      .cancle
        border: 1px solid #E9ECEF;
        color: #666;
      .sub
        background-image: linear-gradient(-138deg, #00DEAF 0%, #00DDB0 19%, #00B1BF 100%);
        color: #fff;
        margin-left: 0.12rem;
        margin-right: 0.36rem;
      .edit
        cursor: pointer;
        display: flex;
        width: 1rem;
        height: 0.36rem;
        justify-content: center;
        align-items: center;
        border: 1px solid #E9ECEF;
        border-radius: 0.04rem;
        color: #20C6BA;
        font-weight: 800;
        font-size: 0.16rem;
        margin-right: 0.2rem;
        .icon-btn_edit
          margin-right: 0.1rem;
    .from
      padding: 0.3rem 0.5rem;
      .formNew
        display: flex;
        justify-content: space-between;
        align-items: center;
        font-size: 0.14rem;
        color: #8597A1;
        height: 0.5rem;
        line-height: 0.5rem;
        background: #FBFBFC;
        border: 1px solid #EAECEF; 
        padding: 0 0.2rem;
        width: 5.4rem;
        white-space: nowrap;
        .right
          display: flex;
          align-items: center;
          .textInp
              width: 0.8rem;
              height: 0.48rem;
              border: 0;
              background: #FBFBFC;
              outline: none;
              text-align: right;
              font-size: 0.16rem;
              color: #263238;
              margin-right: 0.1rem;
          
</style>
<style>
.privateLimit .limitInfo .formNew{
  line-height: 0.5rem;
}
.limitInfo .formNew .el-form-item__label{
  line-height: 0.5rem;
}
.limitInfo .formNew .el-input__inner{
  height: 0.48rem;
  line-height: 0.48rem;
  border: 0;
  background: #FBFBFC;
}
</style>



